  }
}

.phone__link {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 26px;
  line-height: 31px;
  letter-spacing: 0.03em;
  background: #FFFFFF;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: background 0.3s ease 0s;
}
@media (max-width: 1279px) {
  .phone__link {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.02em;
  }
}
@media (max-width: 767px) {
  .phone__link {
    font-size: 20px;
    line-height: 24px;
  }
}

.phone__link:hover,
.phone__link:focus,
.phone__link:active {
  background: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.about {
  padding-top: 51px;
  padding-bottom: 51px;
  background: conic-gradient(from 130.31deg at 16.03% 39.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  color: #FFFFFF;
}
@media (max-width: 1279px) {
  .about {
    padding-bottom: 82px;
  }
}
@media (max-width: 767px) {
  .about {
    padding-top: 51px;
    padding-bottom: 79px;
  }
}
.about h1 {
  order: 1;
  margin: 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 92px;
  line-height: 110px;
  text-align: right;
  letter-spacing: 0.05em;
  margin-top: 15px;
  text-transform: uppercase;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #FFFFFF;
}
@media (max-width: 1279px) {
